<template>
  <section class="h100">
    <h2 class="main-title">
      
    </h2>
    <div class="condition" style="display:flex;align-items:center;">
      <span style="margin: 0 5px;white-space: nowrap"> </span>
      <el-select v-model="classify" placeholder="">
        <el-option v-for="item in classifies" :key="item.value" :label="item.label" :value="item.value"></el-option>
      </el-select>
      <span style="margin: 0 5px;white-space: nowrap"> </span>
      <el-select v-model="dangerName" placeholder="">
        <el-option v-for="item in dangers" :key="item.value" :label="item.label" :value="item.value"></el-option>
      </el-select>
      <span style="margin: 0 5px;white-space: nowrap"> </span>
      <el-date-picker v-model="date" align="right" type="date" placeholder="">
      </el-date-picker>
    </div>
    <!-- end cond -->
    <div class="table">
      <template>
        <el-table :data="info.data" border style="width: 100%">
          <el-table-column prop="classify" label="">
          </el-table-column>
          <el-table-column prop="place" label="">
          </el-table-column>
          <el-table-column prop="danger_name" label="">
          </el-table-column>
          <el-table-column prop="danger_code" label="">
          </el-table-column>
          <el-table-column prop="danger_code" label="">
          </el-table-column>
          <el-table-column prop="danger_code" label="">
          </el-table-column>
        </el-table>
      </template>
    </div>
    <!-- end table -->
    <Pagi :page="page" :pageSize="pageSize" :count="info.count" @pageChange="pageChange" />
    <!-- end page -->
  </section>
</template>
<script>
import Pagi from '@/components/pagi/'

export default {
  data () {
    return {
      classify: '',
      classifies: [{ label: '1', value: 1 }, { label: '2', value: 2 }, { label: '3', value: 3 }],
      dangerName: '',
      dangers: [{ label: '1', value: 1 }, { label: '2', value: 2 }, { label: '3', value: 3 }],
      role: '',
      info: {
        data: [
          { classify: '1', place: '1', danger_name: '1', danger_code: '1', remark: '1' },
          { classify: '1', place: '1', danger_name: '1', danger_code: '1', remark: '1' },
          { classify: '1', place: '1', danger_name: '1', danger_code: '1', remark: '1' },
          { classify: '1', place: '1', danger_name: '1', danger_code: '1', remark: '1' },
          { classify: '1', place: '1', danger_name: '1', danger_code: '1', remark: '1' },
          { classify: '1', place: '1', danger_name: '1', danger_code: '1', remark: '1' }
        ],
        count: 20
      },
      page: 1,
      pageSize: 10,
      date: new Date()
    }
  },
  components: {
    Pagi
  },
  methods: {
    pageChange (page) {
      this.page = page
    }
  },
  watch: {

  },
  created () {
  }
}
</script>
<style lang="less" scoped>
.condition {
  font-size: 14px;
  margin-bottom: 10px;
}
.el-input {
  width: 150px;
}
.table {
  margin: 25px 0;
}
.el-row {
  margin: 10px 0;
}
.lh28 {
  line-height: 28px;
}
pre {
  color: inherit;
  // line-height: 18px;
  font-family: inherit;
}
</style>
<style lang="less">
#edit {
  .el-dialog__body {
    padding: 10px 25px;
  }
  .el-input__inner {
    padding: 0 2px;
    font-size: 14px;
  }
}
.el-row .el-checkbox-group {
  height: 40px;
  display: flex;
  align-items: center;
}
</style>



